import React, { useEffect, useState } from 'react'
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import InputGroup from 'react-bootstrap/InputGroup'
import { registerApi } from '../service/allApis';
import { Await } from 'react-router-dom';


function Add() {

//state to hold image data
const [Image,setImage]=useState(" ")

//state to store image
const [preview,setPreview]=useState(" ")


//create function to store image
const setProfile=(e)=>{
setImage(e.target.files[0]);

}
console.log(Image);




const [userData,setUserData]= useState({
 fname:"",
 email:"",
 lname:"",
 mobile:"",
 gender:"",
 status:"",
 location:""
 
})



//function to store userdata

const userDetails=(e)=>{
//  let value=e.target.value
//  let name=e.target.name
let {value,name}=e.target
   console.log(value);
   console.log(name);

   setUserData({...userData,[name]:value})
   
}
useEffect(()=>{

  if(Image){
    // setPreview(URL.createObjectURL(Image))
    // setPreview(URL.createObjectURL(Image))
  }

},[Image])   // [image] indcate useeffect works when data on image changes

console.log(preview);

const handleSubmit=async(e)=>{
  e.preventDefault()

  
  const headerConfig={
  "Content-Type":"multipart/form-data"
  }

  // body
  const data=new FormData()
const {fname,email
,lname, mobile,
 gender,
 status,
 location

}=userData
//add data in formdata
data.append("user_profile",Image)
data.append("fname",fname)
data.append("lname",lname)
data.append("email",email)
data.append("mobile",mobile)
data.append("gender",gender)
data.append("status",status)
data.append("location",location)

//api call

const response=await registerApi(data,headerConfig)
console.log(response);

console.log({Image});
console.log({fname});
console.log({lname});
console.log({email});
console.log({mobile});
console.log({gender});
console.log({status});
console.log({location});








}



  return (
    <div>

         <div className='container p-3 text-center mt-5  '>
           <h4>Register Employee Details</h4>
         </div>




        <div className='container border p-5 '>
            <div className='text-center mb-5'>
           <img className='align-center' style={{height:'100px' , width:'100px' ,borderRadius:'30px'}} src={ preview ? preview :"https://i.postimg.cc/mrJq2yWr/profil-femme-anonyme-clipart-vecteur-csp43997209.webp"} alt="" />
           </div>
           <Form>
      <Row className="mb-3 leftside">
        <Form.Group as={Col} controlId="firstname">
          <Form.Label className=''>First name</Form.Label>
          <Form.Control onChange={userDetails} name='fname' type="text" placeholder="FirstName" />
         
        </Form.Group>
        

        <Form.Group as={Col} controlId="formGridPassword">
          <Form.Label>LastName</Form.Label>
          <Form.Control onChange={userDetails} name='lname' type="text" placeholder="Last name" />
        </Form.Group>
      </Row>
      <Row className="mb-3">
        <Form.Group as={Col} controlId="formGridEmail">
          <Form.Label>Email</Form.Label>
          <Form.Control onChange={userDetails} name='email' type="email" placeholder="Enter email" />
        </Form.Group>

        <Form.Group as={Col} controlId="formGridPassword">
          <Form.Label>Mobile Number</Form.Label>
          <Form.Control onChange={userDetails} name='mobile' type="text" placeholder="Mobile Number" />
        </Form.Group>
      </Row>
      <Row className="mb-3">
      
        <Form.Group as={Col} className="mb-3">
          <Form.Label as="legend" column sm={2}>
            Gender
          </Form.Label>
          <Col sm={10}>
            <Form.Check
              type="radio"
              label="Male"
              name="gender"
              id="male"
              value={"male"}
              onChange={userDetails}
            />
            <Form.Check
              type="radio"
              label="Female"
              name="gender"
              id="female"
              value={"female"}
              onChange={userDetails}
            />
            
          </Col>
        </Form.Group>
        <Form.Group  as={Col} controlId="formGridState">
          <Form.Label>Status Of Employee</Form.Label>
          <Form.Select onChange={userDetails} name='status' defaultValue={"Active"}>
            <option value={"Active"}>Active</option>
            <option value={"InActive"}>InActive</option>
          </Form.Select>
        </Form.Group>

      </Row>
      <Row className="mb-3 leftside">
      <InputGroup  className='mt-4' as={Col} controlId="formGridState">
        {/* <Button   
           style={{height:'40px'}} variant="primary" id="button-addon1">
          choosefile..
        </Button> */}
        <Form.Control
          // aria-label="Example text with button addon"
          
          aria-describedby="basic-addon1"
         variant="primary"
          type="file"
          onChange={setProfile}
          style={{height:'40px'}}/>
      </InputGroup>
        

        <Form.Group as={Col} controlId="formGridPassword">
          <Form.Label>Employee Location</Form.Label>
          <Form.Control onChange={userDetails} name='location' type="text" placeholder="Enter Employee location" />
        </Form.Group>
      </Row>

<button onClick={handleSubmit} type='submit' className='primary'>Submit</button>
      
    </Form>
    </div>

    </div>
  )
}

export default Add