import React from 'react'
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import InputGroup from 'react-bootstrap/InputGroup';

function Edit() {
    return (
        <div>
            <div>

                <div className='container p-3 text-center mt-5  '>
                    <h4>Edit Employee Details</h4>
                </div>




                <div className='container border p-5 '>
                    <div className='text-center mb-5'>
                        <img className='align-center' style={{ height: '100px', width: '100px', borderRadius: '30px' }} src="https://i.postimg.cc/mrJq2yWr/profil-femme-anonyme-clipart-vecteur-csp43997209.webp" alt="" />
                    </div>
                    <Form>
                        <Row className="mb-3 leftside">
                            <Form.Group as={Col} controlId=" ">
                                <Form.Label className=''>First name</Form.Label>
                                <Form.Control type="text" placeholder="FirstName" />

                            </Form.Group>


                            <Form.Group as={Col} controlId="formGridPassword">
                                <Form.Label>LastName</Form.Label>
                                <Form.Control type="text" placeholder="Last name" />
                            </Form.Group>
                        </Row>
                        <Row className="mb-3">
                            <Form.Group as={Col} controlId="formGridEmail">
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="email" placeholder="Enter email" />
                            </Form.Group>

                            <Form.Group as={Col} controlId="formGridPassword">
                                <Form.Label>Mobile Number</Form.Label>
                                <Form.Control type="text" placeholder="Mobile Number" />
                            </Form.Group>
                        </Row>
                        <Row className="mb-3">

                            <Form.Group as={Col} className="mb-3">
                                <Form.Label as="legend" column sm={2}>
                                    Gender
                                </Form.Label>
                                <Col sm={10}>
                                    <Form.Check
                                        type="radio"
                                        label="Male"
                                        name="formHorizontalRadios"
                                        id="formHorizontalRadios1"
                                    />
                                    <Form.Check
                                        type="radio"
                                        label="Female"
                                        name="formHorizontalRadios"
                                        id="formHorizontalRadios2"
                                    />

                                </Col>
                            </Form.Group>
                            <Form.Group as={Col} controlId="formGridState">
                                <Form.Label>Status Of Employee</Form.Label>
                                <Form.Select defaultValue=" ">
                                    <option value="">Active</option>
                                    <option value="">InActive</option>
                                </Form.Select>
                            </Form.Group>

                        </Row>
                        <Row className="mb-3 leftside">
                            <InputGroup className='mt-4' as={Col} controlId="formGridState">
                                <Button style={{ height: '40px' }} variant="primary" id="button-addon1">
                                    choosefile..
                                </Button>
                                <Form.Control
                                    aria-label="Example text with button addon"
                                    aria-describedby="basic-addon1"
                                    style={{ height: '40px' }} />
                            </InputGroup>


                            <Form.Group as={Col} controlId="formGridPassword">
                                <Form.Label>Employee Location</Form.Label>
                                <Form.Control type="text" placeholder="Enter Employee location" />
                            </Form.Group>
                        </Row>



                    </Form>
                </div>

            </div>
            </div>
            )
}

            export default Edit