// Register API
export const registerApi = async (body, headers) => {
   try {
     const response = await commonRequest("POST", `${BASE_URL}/employees/register`, body, headers);
     return response;
   } catch (error) {
     console.error("Error in registerApi:", error);
     throw error; // You can choose to rethrow the error or handle it differently
   }
 };


