import React from 'react'
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';
import Dropdown from 'react-bootstrap/Dropdown';
import { Link } from 'react-router-dom';
import TableC from './TableC';


function Home() {
  return (
    <div>

   <form action="" className='w-50 '>
    <input type="text" placeholder='Search Employee'  className='p-2 mt-3 ms-5 primary' style={{borderRadius:'10px',borderBlockColor:'#5a05e3'}} />
    <Button variant="success" className='ms-2  p-2'>Search <i class="fa-solid fa-magnifying-glass"></i> </Button>{' '}
   </form>
 <div className='text-end'>
  
<Link to={'add'}>
  <Button variant="success" className='ms-2 p-2 me-5'><i class="fa-solid fa-user-plus "></i><strong className='ms-2'>Add</strong></Button>{' '}
</Link> 

 </div>

 
 <TableC></TableC>
 
 
 </div>


 


 
  )
}

export default Home
